<template>
	<div class="featuresArea"
		:style="{
			height:height+'px'
		}"
	>
		<div class="featuresArea-content">
			<div>
				<div class="featuresArea-content-item" @click="onImg">
					<text class="iconfont icon-icon"></text>	
				</div>
				<view class="featuresArea-content-item_text">
					照片
				</view>
			</div>
			<div>
				<div class="featuresArea-content-item" @click="onCamera">
					<text class="iconfont icon-paishe"></text>	
				</div>
				<view class="featuresArea-content-item_text">
					拍摄
				</view>
			</div>
			<div>
				<div class="featuresArea-content-item">
					<text class="iconfont icon-020-videocallingapp"></text>	
				</div>
				<view class="featuresArea-content-item_text">
					视频通话
				</view>
			</div>
			
			<div>
				<div class="featuresArea-content-item">
					<text class="iconfont icon-shouye"></text>	
				</div>
				<view class="featuresArea-content-item_text">
					定位
				</view>
			</div>
			
			<div>
				<div class="featuresArea-content-item">
					<text class="iconfont icon-shoucang1"></text>	
				</div>
				<view class="featuresArea-content-item_text">
					收藏
				</view>
			</div>
		</div>
		<!-- #ifdef MP-WEIXIN -->
		<template v-if="isShowTaking">
			<camera :device-position="device" :flash="flash" @error="error" :style="{
				width: '100%',
				height: `calc(100vh - ${getHeight}px - 100rpx)`,
				'background-color': '#FFEF84',
				position: 'absolute',
				top:`calc(${getHeight}px + 100rpx)`
			}"></camera>
			
			<!-- 闪光灯 -->
			<cover-image :src="getFlashImg" class="taking-flash-img" :style="{
				top:`calc(${getHeight}px + 100rpx + 30rpx)`
			}" @click="onFlash"></cover-image>
			
			<!-- 底部拍照功能 -->
			<cover-view class="taking">
				<cover-image src="../static/images/箭头.png" class="taking-img" @click="isShowTaking = false"></cover-image>
				<cover-view  class="taking-button" @click="onTaking">
					<cover-image src="../static/images/相机.png" class="taking-img2"></cover-image>
				</cover-view>
				<cover-image src="../static/images/转化.png" class="taking-img1" @click="onDevice"></cover-image>
			</cover-view>	
	
			<!-- 拍照完成预览 -->
			<cover-view v-if="isPreview" :style="{
				height: `calc(100vh - ${getHeight}px - 100rpx)`,
				top:`calc(${getHeight}px + 100rpx)`,
				position: 'absolute',
				width:'100%'
			}">
			<cover-image :src="previewImage"></cover-image>
			<!-- 返回按钮 -->
			<cover-image src="../static/images/返回.png" @click="isPreview = false" class="preview-fanhui"></cover-image>
			
			<cover-view class="preview-send">发送</cover-view>
			</cover-view>
		</template>
		<!-- #endif -->
	</div>
</template>

<script>
	export default{
		props:{
			height:{
				type:Number,
				default:'auto'
			}
		},
		computed:{
			// #ifdef MP-WEIXIN
			getHeight(){
				return this.$getSystemInfoSync().height
			},
			getFlashImg(){
				return this.flash == "off" ? "../static/images/闪光灯关闭.png":"../static/images/闪光灯打开.png"
			}
			// #endif
		},
		data(){
			return {
				isShowTaking:false,
				// 前后摄像头
				device:"back",
				// 闪光灯
				flash:"off",
				// 预览图片
				previewImage:'',
				// 是否开启预览
				isPreview:false
			}
		},
		methods:{
			// 获取图片链接
			onImg(){
				let _this = this
				
				// #ifdef H5
					uni.chooseFile({
					  count: 10,
					  type:'all',
					  extension:['.png','.jpg','.gif','.mp4'],
					  success (res) {
					    res.tempFiles.forEach(el=>{
							el.types = el.type.split("/")[0]
						})
					    _this.$emit("onImg",res)
					  }
					})			
				// #endif
				
				// 微信兼容
				// #ifdef MP-WEIXIN
					let count=0;
					wx.chooseMedia({
						count:9,
						mediaType:['mix'],
						sourceType:['album', 'camera'],
						maxDuration:60,
						success:res=>{
							res.tempFiles.forEach(el=>{
								uni.saveFile({
									tempFilePath:el.tempFilePath,
									success:data=>{
										console.log(data)
										el.savedFilePath = data.savedFilePath;
										count++;
										// 等待所有文件保存在本地
										if(count==res.tempFiles.length){
											_this.$emit("onImg",res)
										}
									}
								})
							})
						}
					})
				// #endif
			},
			// 拍照功能
			onCamera(){
				let _this = this;
				// #ifdef MP-WEIXIN
					this.isShowTaking = true
				// #endif
			},
			onTaking(){
				// #ifdef MP-WEIXIN
				let ctx = uni.createCameraContext();
				ctx.takePhoto({
					quality:"high",
					success:res=>{
						this.previewImage = res.tempImagePath;
						this.isPreview = true
					}
				})
				// #endif
				
				// #ifdef H5
					uni.chooseImage({
						count:1,
						sourceType: ['camera'], 
					    success: function (res) {
					        console.log(res);
					    }
					});
				
				// #endif
				
			},
			// #ifdef MP-WEIXIN
			// 转换摄像头
			onDevice(){
				this.device = this.device == "back" ? "front" : "back"
			},
			// 打开或关闭闪光灯
			onFlash(){
				this.flash = this.flash == "off"?"on":"off"
			}
			// #endif
		}
	}
</script>

<style lang="scss">
	.featuresArea{
		width: 100%;

	}
	.featuresArea-content{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		flex-direction: wrap;
		.featuresArea-content-item{
			display: inline-block;
			padding: 5px 15px;
			background-color: #FFFFFF;
			border-radius: $uni-border-radius-base;
			.iconfont{
				font-size: 30px;
			}
			margin: 10px 30rpx;
		}
		
		.featuresArea-content-item_text{
			font-size: 14px;
			color: $uni-text-color-grey;
			margin-top: 5px;
			text-align: center;
		}
	}
	
		
	.taking{
		text-align: center;
		position: absolute;
		bottom: 22px;
		width: 100%;
		.taking-button{
			display: inline-block;
			width: 150rpx;
			height: 150rpx;
			background-color: #9f9f9f;
			border-radius: $uni-border-radius-circle;
			position: relative;
			.taking-img2{
				width: 70rpx;
				height: 70rpx;
				display: inline-block;
				position: absolute;
				top: 50%;
				transform: translate(-50%,-50%);
				left: 50%;
			}
		}
		.taking-img{
			width: 40rpx;
			height: 40rpx;
			object-fit: cover;
			display: inline-block;
			position: absolute;
			left: calc(50% - 150rpx);
			transform: translateX(-50%);
			top: 50%;
		}
		
		
		.taking-img1{
			width: 40rpx;
			height: 40rpx;
			object-fit: cover;
			display: inline-block;
			position: absolute;
			left: calc(50% + 150rpx);
			transform: translateX(-50%);
			top: 50%;
		}		
	}
	
	.taking-flash-img{
		width: 40rpx;
		height: 40rpx;
		object-fit: cover;
		display: inline-block;
		position: absolute;
		right: 10px;
	}
	
	.preview-fanhui{
		width: 50rpx;
		height: 50rpx;
		object-fit: cover;
		position: absolute;
		top: 30rpx;
		left: 30rpx;
	}
	
	.preview-send{
		position: absolute;
		background-color:$uni-color-primary;
		padding: 10px 15px;
		bottom: 100rpx;
		right: 100rpx;
		border-radius: $uni-border-radius-base;
		color: #FFFFFF;
	}
	
	
</style>
